      activeStep:1,
      activeName: 'first',
      radio:'1',
      tab1step1:true,
      tab1step2:false,
      tab1step3:false,
      tab2step1:true,
      tab2step2:false,
      checkProduct:false,
      productDetail:false,
      buymodal:false,
      tab3step:true
    };
  },
  methods:{
     handleClick(tab, event) {
        console.log(tab, event);
      },
     send1(){
       this.tab1step1=false;
       this.tab1step2=true;
     },
     send2(){
       this.tab1step2=false;
       this.tab1step3=true;
     },
     send3(){
       this.tab1step3=false;
       this.tab1step1=true;
     },
     send4(){
        this.tab1step3=false;
        this.checkProduct=true;
        this.activeStep=2
     },
     goPay(){
       this.activeStep=3;
       this.buymodal=true;
     },
     sendb1(){
       this.tab2step1=false;
       this.tab2step2=true;
     },
     sendb2(){
       this.tab2step2=false;
       this.tab2step1=true;
     },
     sendb3(){
       this.tab2step2=false;
       this.checkProduct=true;
        this.activeStep=2
     },
     type3send(){
        this.checkProduct=true;
        this.activeStep=2;
        this.tab3step=false;
     },
     importExcel(file) {
	  // let file = file.files[0] // input
	  const types = file.name.split('.')[1]
	  const fileType = ['xlsx', 'xlc', 'xlm', 'xls', 'xlt', 'xlw', 'csv'].some(item => item === types)
	  if (!fileType) {
	    this.$message('')
	    return
	  }
	  this.file2Xce(file).then(tabJson => {
	    if (tabJson && tabJson.length > 0) {
	      this.xlsxJson = tabJson
	      console.log('', this.xlsxJson)
	      // xlsxJsonjson,
	      // [
	      //   {
	      //     sheetName: sheet1
	      //     sheet: sheetData
	      //   }
	      // ]
	    }
	  })
	},
	file2Xce(file) {
	  return new Promise(function(resolve, reject) {
	    const reader = new FileReader()
	    reader.onload = function(e) {
	      const data = e.target.result
	      this.wb = XLSX.read(data, {
	        type: 'binary'
	      })
	      const result = []
	      this.wb.SheetNames.forEach((sheetName) => {
	        result.push({
	          sheetName: sheetName,
	          sheet: XLSX.utils.sheet_to_json(this.wb.Sheets[sheetName])
	        })
	      })
	      resolve(result)
	    }
	    reader.readAsBinaryString(file.raw)
	    // reader.readAsBinaryString(file) // input
	  })
  	}
  }
};
</script>

